// Utility functions for mobile detection and interaction

/**
 * Detecta se  um dispositivo mobile baseado em mltiplos critrios
 * Prioriza a largura da tela como critrio principal
 */
export const isMobile = () => {
  // PRIORIDADE: Largura da tela  o critrio principal
  // Se a largura for maior que 768px, NUNCA  mobile (mesmo com touch)
  const width = window.innerWidth;
  if (width > 768) {
    return false; // Desktop - nunca mobile, mesmo com touch
  }
  
  // Para telas menores ou igual a 768px, verificar outros critrios
  const isMobileWidth = width <= 768;
  
  // Verificar se  um dispositivo mvel baseado no user agent
  const isMobileUserAgent = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
  
  // Verificar orientao (mobile geralmente tem orientao varivel)
  const isMobileOrientation = window.orientation !== undefined;
  
  // Verificar se tem capacidades de toque (menos confivel em desktop touch)
  const hasTouch = 'ontouchstart' in window || navigator.maxTouchPoints > 0;
  
  // Se a largura  mobile E tem user agent mobile OU orientao mobile,  mobile
  // Touch sozinho no  suficiente (desktops tambm podem ter touch)
  if (isMobileWidth && (isMobileUserAgent || isMobileOrientation)) {
    return true;
  }
  
  // Se largura  mobile mas no tem user agent nem orientao, 
  // verificar se  realmente mobile (pode ser janela pequena no desktop)
  // Nesse caso, usar apenas a largura como critrio
  return isMobileWidth;
};

/**
 * Detecta se  mobile em orientao paisagem
 */
export const isMobileLandscape = () => {
  return isMobile() && window.innerWidth > window.innerHeight;
};

/**
 * Detecta especificamente se o dispositivo tem capacidades de toque
 */
export const hasTouchCapabilities = () => {
  return 'ontouchstart' in window || navigator.maxTouchPoints > 0;
};

/**
 * Detecta se o evento atual  um evento de toque
 */
export const isTouchEvent = (event) => {
  return event.type && event.type.startsWith('touch');
}; 