import React, { memo } from 'react';
import HandFistIcon from './icons/HandFistIcon';
import InfoSection from './components/InfoSection';
import InfoItem from './components/InfoItem';

// Wrapper para passar className maior ao cone
const LargeHandFistIcon = (props) => <HandFistIcon className="w-8 h-8" {...props} />;

const TerraIndigenaInfo = memo(({ terraIndigena }) => {
  if (!terraIndigena) {
    console.log("TerraIndigenaInfo: terraIndigena  null ou undefined");
    return null;
  }

  const formatarSuperficie = () => {
    return terraIndigena.superficie ? 
      `Superfcie: ${parseFloat(terraIndigena.superficie).toFixed(2)} ha` : null;
  };

  const formatarLocalizacao = () => {
    return `${terraIndigena.municipio || ''}, ${terraIndigena.uf || ''}`.trim();
  };

  return (
    <InfoSection title="Informaes da Terra Indgena" icon={LargeHandFistIcon}>
      <InfoItem label="Povos Indgenas" value={terraIndigena.etnia} />
      <InfoItem label="Localizao" value={formatarLocalizacao()} />
      <InfoItem label="Superfcie" value={formatarSuperficie()} />
      <InfoItem label="Fase" value={terraIndigena.fase} />
      <InfoItem label="Modalidade" value={terraIndigena.modalidade} />
      <InfoItem label="Reestudo" value={terraIndigena.reestudo} />
      <InfoItem label="Coordenao Regional" value={terraIndigena.cr} />
      <InfoItem label="Faixa de Fronteira" value={terraIndigena.faixa_fron} />
      <InfoItem label="Unidade Administrativa" value={terraIndigena.undadm_nom} />
      <InfoItem label="Sigla Unidade" value={terraIndigena.undadm_sig} />
      <InfoItem label="Domnio" value={terraIndigena.dominio_un} />
      <InfoItem label="Data de Atualizao" value={terraIndigena.data_atual} />
      <InfoItem label="Cdigo Terra Indgena" value={terraIndigena.terrai_cod} />
      <InfoItem label="Cdigo Unidade Administrativa" value={terraIndigena.undadm_cod} />
    </InfoSection>
  );
});

export default TerraIndigenaInfo; 